.sign-in {
    margin: 5px;
    width: 400px;
    height: 400px;
    margin-left: 500px;
    border: solid aqua;
    border-radius: 40px;
    padding: 60px;
  }
  
  .sign-in h3 {
    font-size: 30px;
    margin-left: 80px;
  }
  
  .form-control {
    padding: 5px;
    margin: 5px;
    text-align: center;
  }
  
  #email {
    margin-left: 40px;
  }
  
  #password {
    margin-left: 15px;
  }
  
  .sign-in input {
    border-radius: 5px;
  }
  
  .sign-in button {
    font-size: 15px;
    text-align: center;
    width: 260px;
    height: 30px;
    margin-top: 20px;
    margin-left: 3px;
    padding: 10px;
    padding-top: 6px;
    color: black;
    background: white;
    border-radius: 5px;
    cursor: pointer;
  }