.search-box {
    position: absolute;
    left: 300px;
    height: 34px;
    width: 500px;
    margin-bottom: 50px;
    margin-top: 10px;
    border-radius: 5px;
}
.search-btn {
    margin-top: 12px;
    margin-bottom: 10px;
    position: absolute;
    left: 802px;
    top: 90px;
    height: 35px;
    background: blue;
    cursor: pointer;
    border-radius: 5px;
}
.search-btn:hover {
    color: aqua;
    height: 36px;
}