.cart-card {
    width: 100px;
    height: 150px;
    background-color: aliceblue;
    display: flex;
    flex-direction: row;
    margin-top: 0px;
    margin-bottom: 0px;
}


.cart-img img {
    margin: 20px;
    width: 200px;
    height: 100px;
    border-radius: 10px;
}

.cart-heading h3 {
    margin: 50px;
    text-align: center;
}

.cart-quant {
    display: flex;
    margin: 20px;
    align-items: baseline;
    margin-top: 40px;
}
.btn1 {
    width: 30px;
    height: 30px;
    border-radius: 50%;
    margin: 10px;
    cursor: pointer;
}
.btn2 {
    width: 30px;
    height: 30px;
    border-radius: 50%;
    margin: 10px;
    cursor: pointer;
}

.cart-quant span {
    font-size: 20px;
}

.item-price {
    display: flex;
    margin: 20px;
    align-items: baseline;
    margin-top: 30px;
}