.cart-page {
    background: #fff;
    border-radius: 10px;
    margin: 10px;
    width: auto;
    height: 700px;
}
.cart-page h2 {
    position: absolute;
    top: 0px;
    left: 600px;
    margin: 30px;
}

.cart-page cart-list {
    width: 100%;
    height: 100%;
    padding: 10px;
}

.total-sec {
    position: absolute;
    left: 450px;
    display: flex;
    margin: 0px;
    align-items: baseline;
    margin-top: -50px;
}
.item-price {
    display: flex;
    flex-direction: column;
    margin-left: 30px;
    /* margin-top: 0px; */
}
.item-price p {
    margin-top: -10px;
}

.total-price {
    display: flex;
    flex-direction: column;
    margin-left: 30px;
    margin-top: 10px;
}

.checkout {
    margin-top: -10px;
    background: blue;
    padding: 10px;
    border-radius: 20px;
    cursor: pointer;
}

