
.row {
    margin: 0 -5px;
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr;
}

.row::after {
    content: "";
    display: table;
    clear: both;
}

/* Responsive columns */
@media screen and (max-width: 600px) {
    .col {
        width: 100%;
        display: block;
        margin-bottom: 10px;
    }
}

/* card */
.card {
    height: 350px;
    width: 280px;
    box-shadow: 0 8px 8px 0 rgba(0, 0, 0, 0.2);
    padding: 20px;
    text-align: center;
    background-color: #f1f1f1;
    border-radius: 10px;
    margin-bottom: 0px;
    background-color: white;
    margin-top: 60px;
}

.card button {
    width: 100%;
    color: white; 
    background: blue;
    padding: 12px;
    font-size: 18px;
    text-align: center;
    cursor: pointer;
    border: none;
    outline: 0;
    border-radius: 5px;
}

